import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import About from '@/components/About'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'works',
      component: Hello,
      props: (route) => ({ animated: route.query.animated })
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
  ]
})



